<template>
    <div class="prize-wrapper">
        <div class="prize-list">
            <div class="prize-item" v-for="p in prizeList" :class="num == 1 ? 'num1' : (num == 2 ? 'num2' : (num == 3 ? 'num3' : 'num3'))">
                {{p}}
            </div>
        </div>
        <button class="btn btn-default button-prize" @click="start">{{buttonText}}</button>
        <div v-html="time" style="display: none;"></div>
    </div>
</template>

<script>
export default {
    name: 'Draw',
    props: ['count'],
    data () {
        return {
            prizeList: [],
            prizedList: [],
            loading: false,
            buttonText: '开始',
            id: 0,
            mills: 50,
            time: 0,
            num: 1,
            number: 0
        };
    },
    methods: {
        start: function() {
            if (this.loading) {
                this.loading = false;
                this.buttonText = '开始';
                clearTimeout(this.id);
                for (var e of this.prizeList) {
                    this.prizedList.push(e);
                }
                console.log(this.prizedList);
            } else {
                this.number = Number.parseInt(this.count);
                this.loading = true;
                this.buttonText = '停止';
                this.prizeList = [];
                this.randomAll();
            }
        },
        randomAll: function() {
            //this.num = Number.parseInt(this.count);
            for (var i = 0; i < this.num; i++) {
                var rnd = this.random1();
                var index = this.prizedList.indexOf(rnd);
                if (index < 0) {
                    this.prizeList[i] = rnd;
                } else {
                    i--;
                }
            }
            this.time = new Date().getTime();
            this.id = setTimeout(this.randomAll, this.mills);
        },
        random1: function() {
            var rnd = Math.round(Math.random() * this.number);
            if (rnd < 10) {
                return '00' + rnd;
            } else if (rnd >= 10 && rnd < 100) {
                return '0' + rnd;
            } else {
                return '' + rnd;
            }
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.prize-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 0 100px;
    text-align: center;
}

.prize-list {
    width: 100%;
    height: 100%;
    display: table;
}

.prize-item {
    width: 150px;
    height: 100%;
    font-size: 200px;
    vertical-align: middle;
    display: table-cell;
}

.prize-item.num2 {
    font-size: 150px;
}

.prize-item.num3 {
    font-size: 100px;
}

</style>



