<template>
    <div class="cd-wrapper">
        <div class="cd">
            <div class="cd-item">
                {{minText}}
                <span v-if="minute > 0">:</span>
                {{secText}}
            </div>
        </div>
        <button class="btn btn-default button-prize" @click="start">{{buttonText}}</button>
        <div v-html="time" style="display: none;"></div>
        <audio src="static/CanYouFeelIt.m4a" id="audio" controls="controls" ></audio>
    </div>
</template>

<script>
export default {
    name: 'CountDown',
    props: ['countdown'],
    data () {
        return {
            loading: false,
            buttonText: '开始',
            minute: 0,
            second: 0,
            minText: '',
            secText: '',
            mills: 1000,
            time: 0,
            id: 0
        }
    },
    methods: {
        start: function() {
            if (this.loading) {
                this.loading = false;
                this.buttonText = '开始';
                clearTimeout(this.id);
            } else {
                console.log(this.countdown);
                this.init(this.countdown);
                this.loading = true;
                this.buttonText = '停止';
                setTimeout(this.timer, this.mills);
            }
        },
        init: function(n) {
            var ss = n.split('.');
            if (ss.length == 1) {
                this.minute = Number.parseInt(ss[0]) || 0;
                if (this.minute == NaN) {
                    this.minute = 0;
                }
                this.second = 60;
            } else if (ss.length == 2) {
                this.minute = Number.parseInt(ss[0]) || 0;
                this.second = Number.parseInt(ss[1]) || 60;
                if (this.second > 60) {
                    this.second == 60;
                }
            } else {
                this.minute = 0;
                this.second = 0;
                this.minText = '';
                this.secText = '00';
                return;
            }
            this.minText = (this.minute > 0 ? this.minute : '') + '';
            if (this.second == 60) {
                this.secText = '00';
            } else {
                this.secText = (this.second >= 10 ? this.second : '0' + this.second) + '';
            }
        },
        timer: function() {
            if ((this.minute == 0 && (this.second == 0 || this.second == 60))) {
                this.loading = false;
                this.buttonText = '开 始';
                clearTimeout(this.id);
                var audio = document.getElementById('audio');
                audio.play();
                return;
            }
            if (this.second > 0) {
                if (this.second == 60) {
                    this.minute--;
                    this.minText = (this.minute > 0 ? this.minute : '') + '';
                }
                this.second--;
                this.secText = (this.second >= 10 ? this.second : '0' + this.second) + '';
            } else {
                this.second = 59;
                this.secText = '59';
                this.minute--;
                this.minText = (this.minute > 0 ? this.minute : '') + '';
            }
            console.log(this.minute, this.second, this.minText, this.secText)
            this.time = new Date().getTime();
            this.id = setTimeout(this.timer, this.mills);
        }
    },
    mounted: function() {
        this.$watch('countdown', function(n, o) {
            this.init(n);
        })
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cd-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 0 100px;
    text-align: center;
}

.cd {
    width: 100%;
    height: 100%;
    display: table;
}

.cd-item {
    width: 150px;
    height: 100%;
    font-size: 200px;
    vertical-align: middle;
    display: table-cell;
}

.prize-item.num2 {
    font-size: 150px;
}

.prize-item.num3 {
    font-size: 100px;
}
</style>



