<template>
    <div id="app">
        <CountDown v-if="activity == 'countdown'" :countdown="countdown"></CountDown>
        <Draw v-if="activity == 'draw'" :count="count"></Draw>
        <div class="option">
            <div class="option-item" style="position: fixed; left: 10px; bottom: 10px;">
                <input type="radio" name="activity" value="countdown" v-model="activity">倒计时
                <input type="text" class="" size="4" v-model="countdown" placeholder="分钟">
            </div>
            <div class="option-item" style="position: fixed; right: 10px; bottom: 10px;">
                <input type="radio" name="activity" value="draw" v-model="activity">抽奖
                <input type="text" class="" size="4" v-model="count" placeholder="个数">
            </div>
        </div>
    </div>
</template>

<script>
import CountDown from './components/CountDown';
import Draw from './components/Draw';

export default {
    name: 'app',
    components: {
        CountDown, Draw
    },
    data: function() {
        return {
            activity: '',
            countdown: '0.00',
            count: 180,
            prizeList: [],
            loading: false,
            buttonText: '开 始',
            id: 0,
            mills: 100,
            time: 0
        };
    },
    methods: {
        start: function() {
            if (this.loading) {
                this.loading = false;
                this.buttonText = '开 始';
                clearTimeout(this.id);
            } else {
                this.loading = true;
                this.buttonText = '停 止';
                this.randomAll();
            }
        },
        randomAll: function() {
            for (var i = 0; i < this.count; i++) {
                this.prizeList[i] = this.random1();
            }
            this.time = new Date().getTime();
            this.id = setTimeout(this.randomAll, this.mills);
            console.log(this.id);
        },
        random1: function() {
            return Math.round(Math.random() * 100000);
        },
        mixup: function() {

        }
    }
}
</script>

<style>
@import "../static/css/bootstrap.min.css";

html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: Helvetica Neue,Helvetica,Arial,PingFang SC,Heiti SC,Hiragino Sans GB,Microsoft YaHei,\\5FAE\8F6F\96C5\9ED1,sans-serif;
    -webkit-font-smoothing: antialiased;
    color: rgba(0,0,0,.87);
}

#app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #CE3823;
    color: #fff;
}

.option {
    /*position: fixed;
    top: 20px;
    left: 30px;
    text-align: left;
    background: rgba(206, 56, 35, .1);*/
}

 .option-item {
     height: 20px;
     line-height: 20px;
 }

 .option-item input {
     height: 18px;
     color: #ccc;
 }

 .option-item input[type=radio] {
     height:13px;
     vertical-align:text-top;
     margin-top:0;
 }

 .button-prize {
     position: absolute;
     left: 50%;
     margin-left: -30px;
     bottom: 10px;
     color: #333;
     border-radius: 4px;
     border: none;
     background-color: #fff;
 }
</style>



